/**
 * This file is a boilerplate similar to a reset or normalize file, but is more
 * focused on basic UX enhancements rather than eliminating browser defaults.
 */
/**
 * 1. Better default box model (`padding` will now not add to an element's
 *    calculated dimensions, but will instead push content inwards).
 * 2. Remove default spacing on all elements.
 * 3. Respect motion preferences by turning animations and transitions off
 *    according to system settings.
 * 4. Also disable animations and transitions for displays with a low refresh
 *    rate, which would display them poorly.
 * 5. Reduce motion by setting animations and transitions to become
 *    imperceptibly fast, rather than turn them off completely, so that
 *    `transitionend` events still fire.
 * 6. Prevent infinite animations entirely, since infinitely-running high-speed
 *    animations would be undesirable.
 * 7. Reduce motion by removing animated scrolling to anchor elements.
 */
@import url("https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
*::before, *::after {
  box-sizing: border-box;
}
@media screen and (prefers-reduced-motion: reduce), (update: slow) {
  * {
    animation-duration: 0.001ms !important;
    transition-duration: 0.001ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto;
  }
}

/**
 * 1. Prevent horizontal overflow.
 * 2. Prevent viewport overscrolling on touch and track pad devices.
 * 3. Animate scrolling to anchor elements to help users better understand the
 *    direction and location of the target element.
 * 4. Prevent text size changes (triggering reflow) on mobile orientation
 *    changes.
 */
body {
  max-width: 100vw;
  overflow-x: hidden;
  overscroll-behavior-y: none;
  scroll-behavior: smooth;
  text-size-adjust: 100%;
}

/**
 * 1. Remove tapping delay on interactive elements on mobile devices.
 * 2. Remove iOS tap highlight.
 */
a,
button,
input,
label,
select,
[tabindex],
textarea {
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

/**
 * 1. Keep image height proportionate to its width.
 * 2. Prevent images from expanding the parent.
 */
img {
  height: auto;
  max-width: 100%;
}

/**
 * 1. Strengthen the `[hidden]` attribute so that it overrides `display`
 *    properties set within other rulesets.
 */
[hidden] {
  display: none !important;
}

.site-header {
  --header-height: 6rem;
  background: #f9f9fb;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  color: #fff;
  display: flex;
  align-items: center;
  height: var(--header-height);
  position: sticky;
  top: 0;
  z-index: 9;
  padding-top: env(safe-area-inset-top);
}
@media screen and (min-width: 720px) {
  .site-header {
    --header-height: 8rem;
  }
}
.site-header__logo-link {
  display: block;
  width: var(--header-height);
}

.boxed-width {
  margin: 0 auto;
  max-width: 1300px;
  width: calc(100vw - 3rem);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  display: inherit;
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

body {
  color: #3b3b3b;
  font-family: Roboto, sans-serif;
}

main {
  background: #f9f9fb;
  padding-top: 3rem;
}

h1 {
  font-size: clamp(1.5rem, 1.5rem + .75vw, 2rem);
  font-weight: 700;
  margin-bottom: 1rem;
}

.lede {
  color: #3b3b3b;
  font-size: clamp(.8rem, .8rem + .3vw, 1rem);
  font-weight: 300;
  margin-bottom: 2rem;
}

[id=provider-search] {
  appearance: none;
  border: 1px solid #e3e2e2;
  border-radius: 5px;
  box-shadow: 0 1px 1px #e3e2e2;
  color: #3b3b3b;
  font-family: inherit;
  font-size: clamp(1rem, 1rem + .3vw, 1.25rem);
  font-weight: 500;
  margin-bottom: 2rem;
  padding: 1rem 1.5rem;
  width: 100%;
}
[id=provider-search]::placeholder {
  color: #3b3b3b;
}

.provider-search-container {
  position: relative;
}

.provider-search-icon {
  background-color: #da2531;
  width: 1.75rem;
  height: 1.75rem;
  border-radius: 1.75rem;
  padding: 0.3rem;
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-100%);
}

[id=provider-results] {
  list-style: none;
}

.provider-result {
  animation: expand 0.5s cubic-bezier(0, 1, 0.4, 1);
  overflow: hidden;
  padding: 2rem 0;
}
.provider-result:nth-of-type(even) {
  background: #fff;
}
.provider-result[data-transition=hiding] {
  animation: shrink 0.7s cubic-bezier(0, 1, 0.4, 1);
  transform-origin: center;
}
.provider-result[data-transition=hiding] .provider-result__icon {
  display: none;
}
@keyframes expand {
  0% {
    font-size: 0;
    opacity: 0;
    padding: 0;
  }
  27% {
    font-size: initial;
    opacity: 0;
    padding: 2rem 0;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes shrink {
  to {
    height: 0;
    opacity: 0;
    padding: 0;
  }
}
.provider-result .boxed-width {
  display: flex;
  gap: 1rem;
}
.provider-result__name {
  font-size: clamp(1rem, 1rem + .3vw, 1.25rem);
  font-weight: 700;
  margin-bottom: 0.75rem;
}
.provider-result__sub {
  color: #3b3b3b;
  font-size: clamp(.8rem, .8rem + .3vw, 1rem);
  font-weight: 300;
}
.provider-result__icon {
  border-radius: 1.75rem;
  display: flex;
  background: #8bb455;
  height: 1.75rem;
  width: 1.75rem;
  flex-shrink: 0;
  margin-left: auto;
  padding: 0.3rem;
}

.no-results-message {
  font-size: clamp(1rem, 1rem + .3vw, 1.25rem);
  font-weight: 500;
  padding: 2rem 0 4rem;
}